<template>
	<div class="login">
		<header></header>
		<section>
			<div class="registerbody">
				<div class="title">
					<p>注册</p>
				</div>
				<form>
					<div class="tel">
						<span>+86</span>
						<img src="../../static/images/rightArrow.png"/>
						<input type="text" placeholder="请输入您的手机号码" v-model.number="pn"/>
					</div>
					<div class="code">
						<input type="text" placeholder="请输入短信验证码" v-model="codeText"/>
						<button class="button1" @click="getcode()">{{mydate}}</button>
					</div>
					<div class="pas">
						<input :type="pswType" placeholder="请输入您的密码" v-model="psdText"/>
						<img :src="'../../static/images/'+pswImg+'.png'" @click="seepsw()" />
					</div>
				</form>
				<button class="button2" @click="getres()">
					登录
				</button>
				<p class="agree"><span>注册即代表您同意好礼多多
</span><span>【用户协议】</span></p>
			</div>
		</section>
		<div class="erro" v-if="iserro">{{erroText}}</div>
		<footer></footer>
	</div>
</template>

<script>
	import axios from "axios"
	export default {
		data() {
			return {
				pswType:'password',
				pswImg:'seepwd',
				pn:'',
				newCountry:'+86',
				iserro:false,
				erroText:'',
				mydate:'获取验证码',
				islogin:false,
				psdText:'',
				codeText:''
			}
		},
		methods:{
			seepsw(){
				if(this.pswImg=="seepwd"){
					this.pswImg="pwd";
					this.pswType="text";					
				}else{
					this.pswImg="seepwd";
					this.pswType="password";
				}
			},
			getcode(){
				let that = this	
				var _timeSpan = this.uuid();
				var _sign=this.md5(_timeSpan+"hldd"+this.pn)
				//var pn = this.pn
				var newCountry = this.newCountry
				if(/^((13[0-9])|(14[5|7])|(15([0-3]|[5-9]))|(18[0,2,5-9]))\d{8}$/.test(this.pn)){
					var url =`/api/api/index.php?c=login&a=register_sms&from=web&phone=${this.pn}&timespan=${_timeSpan}&sign=${_sign}&country_code=${newCountry}&appversion=1.3.0&devicetype=weixin&phonetype=a`
		            axios.get(url)
		              .then((res)=> {
		              	if(res.data.code==200){
		              		this.mydate=59;
		              		console.log(res);
		              		var timer = setInterval(function(){
		              			this.mydate--;
		              			if(this.mydate==0){
		              				clearInterval(timer);
		              				this.mydate='重新获取'
		              			}
		              		}.bind(that),1000)
		              	}
		            })
				}else{
					this.iserro=true;
					this.erroText='请输入正确的手机号码'
				}
			},
			getres(){
				var that = this
				if(this.codeText!=''){
					if(this.psdText!=""){
						if( /^[0-9,a-z,A-Z]{6,16}$/.test(this.psdText) ){
						    var params = new URLSearchParams();
				            params.append('client', 'IOS');
				            params.append('code', this.codeText);
				            params.append('mobile', this.pn);
				            params.append('password', this.md5(this.psdText));
				            params.append('country_code', this.newCountry);
				            params.append('client_id', '0');
				            axios.post(`/api/api/index.php?c=login&a=register`,params,{
				                  headers:{
				                     'content-type':'application/x-www-form-urlencoded',
				                     charset:'UTF-8'
				                  }
				              })
				              .then((res)=>{
				              	if(res.data.code==200){
				              		this.$store.commit('loginkey',res.data.datas.key);
				              		localStorage.setItem("HLDD_KEY",res.data.datas.key)
				              		localStorage.setItem("HLDD_ID",res.data.datas.uid)
				              		localStorage.setItem("HLDD_PH",res.data.datas.mobile)
				              		localStorage.setItem("HLDD_NAME",res.data.datas.nickname)
							localStorage.setItem("HLDD_OK",true)
							this.$store.commit('loginkey',res.data.datas.key)      
				              		this.$router.push({path:'/mine'})
				              	}
				              	console.log(res)
				              })
						}else{
							this.iserro=true;
							this.erroText='密码格式为6-16位英文字母及数字'
							setTimeout(function(){
								that.iserro=false;
							}.bind(that),2000)
						}
					} else {
						this.iserro=true;
						that.erroText='密码不能为空'
						setTimeout(function(){
							that.iserro=false;
						}.bind(that),2000)
					}
				} else {
					this.iserro=true;
					this.erroText='请输入验证码'
					setTimeout(function(){
						that.iserro=false;
					}.bind(that),2000)
				}
/*				axios.get(
					`/api/api/index.php?c=login&a=register&client=IOS&code=2134&mobile=&password=45c4410493f4697efe34095e745d84c3&country_code=%2B86&client_id=0&invite_code=&appversion=1.3.0&devicetype=weixin&phonetype=android`,
				).then((res)=>{
					console.log(res);
				})*/
			},
			uuid(){
			    var s = [];
			    var hexDigits = "0123456789abcdef";
			    for (var i = 0; i < 16; i++) {
			        s[i] = hexDigits.substr(Math.floor(Math.random() * 0x10), 1);
			    }
			    var uuid = s.join("");
			    return uuid;
			},
			md5(string){
                function md5_RotateLeft(lValue, iShiftBits) {
                        return (lValue<<iShiftBits) | (lValue>>>(32-iShiftBits));
                }
                function md5_AddUnsigned(lX,lY){
                        var lX4,lY4,lX8,lY8,lResult;
                        lX8 = (lX & 0x80000000);
                        lY8 = (lY & 0x80000000);
                        lX4 = (lX & 0x40000000);
                        lY4 = (lY & 0x40000000);
                        lResult = (lX & 0x3FFFFFFF)+(lY & 0x3FFFFFFF);
                        if (lX4 & lY4) {
                                return (lResult ^ 0x80000000 ^ lX8 ^ lY8);
                        }
                        if (lX4 | lY4) {
                                if (lResult & 0x40000000) {
                                        return (lResult ^ 0xC0000000 ^ lX8 ^ lY8);
                                } else {
                                        return (lResult ^ 0x40000000 ^ lX8 ^ lY8);
                                }
                        } else {
                                return (lResult ^ lX8 ^ lY8);
                        }
		                }         
		                function md5_F(x,y,z){
		                        return (x & y) | ((~x) & z);
		                }
		                function md5_G(x,y,z){
		                        return (x & z) | (y & (~z));
		                }
		                function md5_H(x,y,z){
		                        return (x ^ y ^ z);
		                }
		                function md5_I(x,y,z){
		                        return (y ^ (x | (~z)));
		                }
		                function md5_FF(a,b,c,d,x,s,ac){
		                        a = md5_AddUnsigned(a, md5_AddUnsigned(md5_AddUnsigned(md5_F(b, c, d), x), ac));
		                        return md5_AddUnsigned(md5_RotateLeft(a, s), b);
		                }; 
		                function md5_GG(a,b,c,d,x,s,ac){
		                        a = md5_AddUnsigned(a, md5_AddUnsigned(md5_AddUnsigned(md5_G(b, c, d), x), ac));
		                        return md5_AddUnsigned(md5_RotateLeft(a, s), b);
		                };
		                function md5_HH(a,b,c,d,x,s,ac){
		                        a = md5_AddUnsigned(a, md5_AddUnsigned(md5_AddUnsigned(md5_H(b, c, d), x), ac));
		                        return md5_AddUnsigned(md5_RotateLeft(a, s), b);
		                }; 
		                function md5_II(a,b,c,d,x,s,ac){
		                        a = md5_AddUnsigned(a, md5_AddUnsigned(md5_AddUnsigned(md5_I(b, c, d), x), ac));
		                        return md5_AddUnsigned(md5_RotateLeft(a, s), b);
		                };
		                function md5_ConvertToWordArray(string) {
		                        var lWordCount;
		                        var lMessageLength = string.length;
		                        var lNumberOfWords_temp1=lMessageLength + 8;
		                        var lNumberOfWords_temp2=(lNumberOfWords_temp1-(lNumberOfWords_temp1 % 64))/64;
		                        var lNumberOfWords = (lNumberOfWords_temp2+1)*16;
		                        var lWordArray=Array(lNumberOfWords-1);
		                        var lBytePosition = 0;
		                        var lByteCount = 0;
		                        while ( lByteCount < lMessageLength ) {
		                                lWordCount = (lByteCount-(lByteCount % 4))/4;
		                                lBytePosition = (lByteCount % 4)*8;
		                                lWordArray[lWordCount] = (lWordArray[lWordCount] | (string.charCodeAt(lByteCount)<<lBytePosition));
		                                lByteCount++;
		                        }
		                        lWordCount = (lByteCount-(lByteCount % 4))/4;
		                        lBytePosition = (lByteCount % 4)*8;
		                        lWordArray[lWordCount] = lWordArray[lWordCount] | (0x80<<lBytePosition);
		                        lWordArray[lNumberOfWords-2] = lMessageLength<<3;
		                        lWordArray[lNumberOfWords-1] = lMessageLength>>>29;
		                        return lWordArray;
		                }; 
		                function md5_WordToHex(lValue){
		                        var WordToHexValue="",WordToHexValue_temp="",lByte,lCount;
		                        for(lCount = 0;lCount<=3;lCount++){
		                                lByte = (lValue>>>(lCount*8)) & 255;
		                                WordToHexValue_temp = "0" + lByte.toString(16);
		                                WordToHexValue = WordToHexValue + WordToHexValue_temp.substr(WordToHexValue_temp.length-2,2);
		                        }
		                        return WordToHexValue;
		                };
		                function md5_Utf8Encode(string){
		                        string = string.replace(/\r\n/g,"\n");
		                        var utftext = ""; 
		                        for (var n = 0; n < string.length; n++) {
		                                var c = string.charCodeAt(n); 
		                                if (c < 128) {
		                                        utftext += String.fromCharCode(c);
		                                }else if((c > 127) && (c < 2048)) {
		                                        utftext += String.fromCharCode((c >> 6) | 192);
		                                        utftext += String.fromCharCode((c & 63) | 128);
		                                } else {
		                                        utftext += String.fromCharCode((c >> 12) | 224);
		                                        utftext += String.fromCharCode(((c >> 6) & 63) | 128);
		                                        utftext += String.fromCharCode((c & 63) | 128);
		                                } 
		                        } 
		                        return utftext;
		                }; 
		                var x=Array();
		                var k,AA,BB,CC,DD,a,b,c,d;
		                var S11=7, S12=12, S13=17, S14=22;
		                var S21=5, S22=9 , S23=14, S24=20;
		                var S31=4, S32=11, S33=16, S34=23;
		                var S41=6, S42=10, S43=15, S44=21;
		                string = md5_Utf8Encode(string);
		                x = md5_ConvertToWordArray(string); 
		                a = 0x67452301; b = 0xEFCDAB89; c = 0x98BADCFE; d = 0x10325476; 
		                for (k=0;k<x.length;k+=16) {
		                        AA=a; BB=b; CC=c; DD=d;
		                        a=md5_FF(a,b,c,d,x[k+0], S11,0xD76AA478);
		                        d=md5_FF(d,a,b,c,x[k+1], S12,0xE8C7B756);
		                        c=md5_FF(c,d,a,b,x[k+2], S13,0x242070DB);
		                        b=md5_FF(b,c,d,a,x[k+3], S14,0xC1BDCEEE);
		                        a=md5_FF(a,b,c,d,x[k+4], S11,0xF57C0FAF);
		                        d=md5_FF(d,a,b,c,x[k+5], S12,0x4787C62A);
		                        c=md5_FF(c,d,a,b,x[k+6], S13,0xA8304613);
		                        b=md5_FF(b,c,d,a,x[k+7], S14,0xFD469501);
		                        a=md5_FF(a,b,c,d,x[k+8], S11,0x698098D8);
		                        d=md5_FF(d,a,b,c,x[k+9], S12,0x8B44F7AF);
		                        c=md5_FF(c,d,a,b,x[k+10],S13,0xFFFF5BB1);
		                        b=md5_FF(b,c,d,a,x[k+11],S14,0x895CD7BE);
		                        a=md5_FF(a,b,c,d,x[k+12],S11,0x6B901122);
		                        d=md5_FF(d,a,b,c,x[k+13],S12,0xFD987193);
		                        c=md5_FF(c,d,a,b,x[k+14],S13,0xA679438E);
		                        b=md5_FF(b,c,d,a,x[k+15],S14,0x49B40821);
		                        a=md5_GG(a,b,c,d,x[k+1], S21,0xF61E2562);
		                        d=md5_GG(d,a,b,c,x[k+6], S22,0xC040B340);
		                        c=md5_GG(c,d,a,b,x[k+11],S23,0x265E5A51);
		                        b=md5_GG(b,c,d,a,x[k+0], S24,0xE9B6C7AA);
		                        a=md5_GG(a,b,c,d,x[k+5], S21,0xD62F105D);
		                        d=md5_GG(d,a,b,c,x[k+10],S22,0x2441453);
		                        c=md5_GG(c,d,a,b,x[k+15],S23,0xD8A1E681);
		                        b=md5_GG(b,c,d,a,x[k+4], S24,0xE7D3FBC8);
		                        a=md5_GG(a,b,c,d,x[k+9], S21,0x21E1CDE6);
		                        d=md5_GG(d,a,b,c,x[k+14],S22,0xC33707D6);
		                        c=md5_GG(c,d,a,b,x[k+3], S23,0xF4D50D87);
		                        b=md5_GG(b,c,d,a,x[k+8], S24,0x455A14ED);
		                        a=md5_GG(a,b,c,d,x[k+13],S21,0xA9E3E905);
		                        d=md5_GG(d,a,b,c,x[k+2], S22,0xFCEFA3F8);
		                        c=md5_GG(c,d,a,b,x[k+7], S23,0x676F02D9);
		                        b=md5_GG(b,c,d,a,x[k+12],S24,0x8D2A4C8A);
		                        a=md5_HH(a,b,c,d,x[k+5], S31,0xFFFA3942);
		                        d=md5_HH(d,a,b,c,x[k+8], S32,0x8771F681);
		                        c=md5_HH(c,d,a,b,x[k+11],S33,0x6D9D6122);
		                        b=md5_HH(b,c,d,a,x[k+14],S34,0xFDE5380C);
		                        a=md5_HH(a,b,c,d,x[k+1], S31,0xA4BEEA44);
		                        d=md5_HH(d,a,b,c,x[k+4], S32,0x4BDECFA9);
		                        c=md5_HH(c,d,a,b,x[k+7], S33,0xF6BB4B60);
		                        b=md5_HH(b,c,d,a,x[k+10],S34,0xBEBFBC70);
		                        a=md5_HH(a,b,c,d,x[k+13],S31,0x289B7EC6);
		                        d=md5_HH(d,a,b,c,x[k+0], S32,0xEAA127FA);
		                        c=md5_HH(c,d,a,b,x[k+3], S33,0xD4EF3085);
		                        b=md5_HH(b,c,d,a,x[k+6], S34,0x4881D05);
		                        a=md5_HH(a,b,c,d,x[k+9], S31,0xD9D4D039);
		                        d=md5_HH(d,a,b,c,x[k+12],S32,0xE6DB99E5);
		                        c=md5_HH(c,d,a,b,x[k+15],S33,0x1FA27CF8);
		                        b=md5_HH(b,c,d,a,x[k+2], S34,0xC4AC5665);
		                        a=md5_II(a,b,c,d,x[k+0], S41,0xF4292244);
		                        d=md5_II(d,a,b,c,x[k+7], S42,0x432AFF97);
		                        c=md5_II(c,d,a,b,x[k+14],S43,0xAB9423A7);
		                        b=md5_II(b,c,d,a,x[k+5], S44,0xFC93A039);
		                        a=md5_II(a,b,c,d,x[k+12],S41,0x655B59C3);
		                        d=md5_II(d,a,b,c,x[k+3], S42,0x8F0CCC92);
		                        c=md5_II(c,d,a,b,x[k+10],S43,0xFFEFF47D);
		                        b=md5_II(b,c,d,a,x[k+1], S44,0x85845DD1);
		                        a=md5_II(a,b,c,d,x[k+8], S41,0x6FA87E4F);
		                        d=md5_II(d,a,b,c,x[k+15],S42,0xFE2CE6E0);
		                        c=md5_II(c,d,a,b,x[k+6], S43,0xA3014314);
		                        b=md5_II(b,c,d,a,x[k+13],S44,0x4E0811A1);
		                        a=md5_II(a,b,c,d,x[k+4], S41,0xF7537E82);
		                        d=md5_II(d,a,b,c,x[k+11],S42,0xBD3AF235);
		                        c=md5_II(c,d,a,b,x[k+2], S43,0x2AD7D2BB);
		                        b=md5_II(b,c,d,a,x[k+9], S44,0xEB86D391);
		                        a=md5_AddUnsigned(a,AA);
		                        b=md5_AddUnsigned(b,BB);
		                        c=md5_AddUnsigned(c,CC);
		                        d=md5_AddUnsigned(d,DD);
		                }
				        return (md5_WordToHex(a)+md5_WordToHex(b)+md5_WordToHex(c)+md5_WordToHex(d)).toLowerCase();
				}
		}
	}
</script>

<style lang="scss">
@import "../styles/yo/usage/core/reset";
@import "../styles/modules/common.scss";
.login{
	width:100%;
	height:100%;
	position:relative;
	.erro{
		position:fixed;
		top:46%;
		left:10%;
		width:80%;
		background: #F2F2F2;
		text-align:center;
		height:.3rem;
		line-height:.3rem;
		border-radius:.05rem;
	}
	section{
		width: 100%;
		height:100%;
		.registerbody{
			margin-top:.4rem;
			height:50%;
			width: 100%;
			.title{
				text-align: center;
				p{
				    font-size: 20px;
					font-weight: bold;
					margin-bottom: .4rem;
				}
			}
			form{
				width: 100%;
				margin-top:.15rem;
				padding:0 .15rem;
				height:.9rem;
				.tel{
					height:.45rem;
					width: 100%;
					border-top: #d6d2cf 1px solid;
					position:relative;
					span:nth-child(1){
						margin-left: .1rem;
						font-weight: bold;
						text-align: left;
						font-size: 16px;
						line-height: 45px;
					}
					img{
						position: relative;
						height:.36rem;
						top:-0.02rem;
						left: .2rem;
					}
					input{
						margin-left: .1rem;
						border: none;
						width: 2.4rem;
						height:99%;
					    padding: 14px 10px;
					    font-size: 15px;
					    
					}
				}
				.code{
					height:.45rem;
					width: 100%;
					border-top: #d6d2cf 1px solid;
					position: relative;
					input{
						width:50%;
						border: none;
						height:100%;
						padding-left: .1rem;
					}
					.button1{
						position: absolute;
						right:0;
						top:0;
						height:100%;
						width: 1rem;
						color: white;
						background: #686868;
						border: none;
					}
				}
				.pas{
					display: block;
					height:.45rem;
					width: 100%;
					border-top: #d6d2cf 1px solid;
					border-bottom: 1px solid #D6D2CF;
					position: relative;
					input{
						width: 100%;
						height:100%;
						border: none;
						padding-left: .1rem;
					}
					img{
						height:.2rem;
						position: absolute;
						right:.1rem;
						top:.125rem;
					}
				}
			}
			.button2{
				width:92%;
				height:.45rem;
				margin-left: 4%;
				margin-top:1.2rem;
				background: #181818;
				border: none;
				@include border-radius(.04rem);
				color: white;
				position: relative;
			}
			.agree{
				text-align: center;
				font-size: 12px;
				margin-top:.2rem;
				span:nth-child(2){
					color: #BC914D;
				}
			}
		}
	}
}
</style>


